* {
  margin: 10px;
  background-color: #2f3136;
  color: whitesmoke;
  font-family: "Belanosima", sans-serif;
}

.app-wrapper-content {
  grid-area: c;
  background-color: #45484e;
  color: whitesmoke;
  border-radius: 15px;
  font-family: "Belanosima", sans-serif;
}

.app-wrapper-content div {
}

.app-wrapper-content img {
  width: 700px;
}

.app-wrapper {
  display: grid;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 115px 1fr;
  grid-template-columns: 2fr 9fr;
  grid-row-gap: 30px;
  grid-column-gap: 20px;
  font-size: 24px;
}
