.header {
  grid-area: h;
  background-color: #45484e;
  color: whitesmoke;
  border-radius: 15px;
  font-family: 'Belanosima', sans-serif;
}

.header img {
  width: 70px;
  background-color: #45484e;

}

.header a {
  background-color: #45484e;
}

.wrapper {
  float: right;
  background-color: #45484e;
  font-size: 32px;
  margin-top: 20px;
  margin-right: 30px;
}

.editButton {
  appearance: none;
  border: 0;
  border-radius: 5px;
  background: #4676D7;
  color: #fff;
  padding: 12px 24px;
  font-size: 16px;
  cursor: pointer;
  min-width: 120px;
  height: max-content;
}

.editButton:hover {
  background: #1d49aa;
}

.editButton:focus {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}