.numbers:hover {
    border: 1px solid #4676D7;
    border-radius: 10px;
    color: #4676D7;
    transition: all 0.5s;
}

.numbers {
    padding: 6px 10px;
    cursor: pointer;
    border: 1px solid #45484e;
}

.arrow {
    font-size: 20px;
    margin: 0;
    padding: 5px 10px;
    background-color: #45484e;
    border: 1px solid #45484e;
    cursor: pointer;
}

.arrow:hover {
    border: 1px solid #4676D7;
    border-radius: 50px;
    transition: all 0.5s;
}