.wrap {
    background-color: #45484e;
}

.wrapper {
    background-color: #45484e;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
}

.wrapping {
    background-color: #45484e;
    display: flex;
    align-items: center;
}

.name {
    background-color: #45484e;
}

.color {
    background-color: #45484e;
    margin: 0;
}

.textarea {
    color: #212529;
    width: 30%;
    height: 30px;
    background-color: #fff;
    border-color: #bdbdbd;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
    resize: none;
    border-radius: 5px;
    font-size: 20px;
}

.select {
    color: #212529;
    width: 12%;
    height: 20px;
    background-color: #fff;
    border-color: #bdbdbd;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
    border-radius: 5px;
    font-size: 16px;
}

.editButton {
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676D7;
    color: #fff;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    min-width: 120px;
    height: max-content;
}

.editButton:hover {
    background: #1d49aa;
}

.editButton:focus {
    outline: none;
    box-shadow: 0 0 0 4px #cbd6ee;
}

.followButton {
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676D7;
    color: #fff;
    padding: 6px 12px;
    font-size: 16px;
    cursor: pointer;
    min-width: 100px;
    height: max-content;
}

.followButton:hover {
    background: #1d49aa;
}

.followButton:focus {
    outline: none;
    box-shadow: 0 0 0 4px #cbd6ee;
}